html,
body {
  height: 100%;
  box-sizing: border-box;
  font-size: 20px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: "Open Sans", sans-serif;
  line-height: 1.6;
}

:root * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
}

.wrapper {
  width: 100%;
  font-family: "Open Sans", sans-serif;
}

img {
  max-width: 100%;
}

.icon {
  fill: #1d363b;
}

.c_sand {
  color: #dfc15e;
}

.section {
  position: relative;
}

@media (min-width: 992px) {
  .container.container--wide {
    max-width: 1366px;
  }
}

@media (min-width: 768px) {
  .container.container--wide {
    max-width: 1366px;
  }
}

.header {
  padding: 8px 0;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: #ffffff;
}

.header .icon-main-logo {
  width: 190px;
}

.header .navbar {
  max-width: 1790px;
  margin: 0 auto;
}

.header__logo .icon-main-logo {
  font-size: 2em;
}

@media only screen and (max-width: 992px) {
  .header .main-menu {
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
  }
}

@media only screen and (min-width: 992px) {
  .header .nav-item:not(:first-child) {
    margin-left: 1.5em;
  }
}

.header .nav-link {
  font-size: 16px;
  padding: 0.2em 0.5em;
  color: #1d363b;
  line-height: 1.31;
  transition: all 0.3s ease;
}

.header .nav-link:hover {
  color: #030607;
}

.header .nav-item--cart {
  margin-left: 0.5em !important;
  border-left: solid 1px #e1e1e1;
}

.header .nav-item--cart .nav-link span {
  color: #e1e1e1;
}

.navbar-toggler {
  border: none;
  color: #1d363b;
}

.navbar-toggler:focus {
  outline: none !important;
}

.navbar-toggler-icon {
  width: 1.315em;
  height: 2em;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 26.325 19'%3E%3Cdefs%3E%3Cstyle%3E.a,.b,.d{fill:none;}.a,.b{stroke:%231d363b;}.b{stroke-linejoin:round;}.c{stroke:none;}%3C/style%3E%3C/defs%3E%3Cg transform='translate(-323 -14)'%3E%3Cline class='a' x1='9.132' transform='translate(331.596 20.5)'/%3E%3Cline class='a' x1='9.132' transform='translate(331.596 26.5)'/%3E%3Cg class='b' transform='translate(323 14)'%3E%3Crect class='c' width='26.325' height='19'/%3E%3Crect class='d' x='0.5' y='0.5' width='25.325' height='18'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

.carousel-wrapper {
  overflow: hidden;
}

.carousel-wrapper .slick-next {
  right: 25px;
}

.carousel-wrapper .slick-prev {
  left: 25px;
  z-index: 1;
}

.map-canvas {
  width: 100%;
  height: 300px;
}

picture source,
picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.bg-absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: grey;
}

.bg-absolute img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

img {
  border: 0;
}

img:not([src]):not([srcset]) {
  visibility: hidden;
}

.lazyload {
  animation-name: fade;
  animation-duration: 1s;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

a {
  transition: all 0.3s ease;
  text-decoration: none;
  color: inherit;
}

.form-control:focus {
  box-shadow: none;
  border-color: #1d363b;
}

/* Button Primary */
.btn-primary {
  color: #fff;
  background-color: #1d363b;
  border-color: #1d363b;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 16px;
}

.btn-primary:hover {
  color: #fff;
  background-color: #284b52;
  border-color: #284b52;
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle {
  background-color: #d3e6e9;
  border-color: #d3e6e9;
}

.btn-primary.focus,
.btn-primary:focus,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: none;
}

/* Button Outline Primary */
.btn-outline-primary {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 178px;
  height: 40px;
  line-height: 1;
  color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 16px;
  border: 2px solid rgba(235, 235, 235, 0.8);
  border-radius: 0px;
  padding: 0.5rem 1rem;
}

.btn-outline-primary:hover {
  background-color: #112226;
  border-color: #112226;
}

.btn-outline-primary:not(:disabled):not(.disabled).active,
.btn-outline-primary:not(:disabled):not(.disabled):active,
.show > .btn-outline-primary.dropdown-toggle {
  background-color: #112226;
  border-color: #112226;
}

.btn-outline-primary.focus,
.btn-outline-primary:focus,
.btn-outline-primary:not(:disabled):not(.disabled).active:focus,
.btn-outline-primary:not(:disabled):not(.disabled):active:focus,
.show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: none;
}

/* End of Button Outline Primary */
/* Header */
.header__current-time {
  flex-grow: 1;
}

@media only screen and (max-width: 992px) {
  .header__current-time {
    display: none;
  }
}

.time-country {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  margin-bottom: 0;
  color: #1d363b;
  font-weight: 400;
  display: inline-block;
  margin-top: 12px;
}

.time-country img {
  width: 13px;
  margin: 0px 10px;
}

.time-country span {
  font-family: "Open Sans", sans-serif;
  font-style: italic;
}

/* End of Header */
/* Slider */
.slick-slide {
  height: auto;
}

.slide {
  position: relative;
  padding-top: 8rem;
}

@media only screen and (max-width: 480px) {
  .slide {
    padding: 3rem 1rem 10rem 0;
  }
}

.slide__text {
  padding-left: 5vw;
  color: #fff;
}

.slide__subtitle {
  margin-bottom: 1rem;
  font-size: 2rem;
}

@media only screen and (max-width: 480px) {
  .slide__subtitle {
    font-size: 1.5em;
  }
}

.slide__title {
  margin-bottom: 2rem;
  font-family: "Open Sans", sans-serif;
  font-size: 2.5rem;
  font-weight: 600;
  line-height: 1.1;
}

@media only screen and (max-width: 480px) {
  .slide__title {
    font-size: 2rem;
  }
}

.slide__text .btn {
  margin-top: 1rem;
}

.slick-dots {
  position: absolute;
  bottom: 15%;
  left: 5vw;
  margin: 0;
  text-align: left;
}

@media only screen and (max-width: 480px) {
  .slick-dots {
    display: none;
  }
}

.slick-dots li {
  padding: 0;
  width: 13px;
  height: 13px;
}

.slick-dots li button {
  width: 13px;
  height: 13px;
  margin: 0;
  opacity: 0.44;
  background-color: #ffffff;
}

.slick-dots li button:before {
  display: none;
}

.slick-dots li.slick-active button {
  opacity: 1;
}

.owl-theme .owl-dots .owl-dot {
  outline: none;
}

.slick-dotted.slick-slider {
  margin-bottom: 0;
}

/* End of Slider */
/* Newly Arrived */
.newly-arrived {
  margin: 3rem 0;
  padding: 3rem 0 0;
  position: relative;
}

.newly-arrived .content {
  padding-left: 20px;
}

.light-bg {
  background: #f8f8f8;
  width: 95%;
  height: 80%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}

@media only screen and (max-width: 480px) {
  .light-bg {
    height: 86%;
    width: 82%;
  }
}

.content {
  color: #1d363b;
}

.content__title {
  font-family: "Open Sans", sans-serif;
  font-size: 2rem;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 480px) {
  .content__title {
    font-size: 1.5rem;
  }
}

.content__text {
  font-family: "Lora", serif;
  font-size: 1rem;
  line-height: 1.5;
}

.newly-arrived-box {
  margin-top: 2rem;
  cursor: pointer;
  position: relative;
  transition: all 0.35s;
}

.newly-arrived-box:hover .newly-arrived-box-hover {
  opacity: 1;
}

.newly-arrived-box .img-fluid {
  display: block;
  margin: 0 auto;
  transition: all 0.35s;
}

.newly-arrived-box-hover {
  display: none;
}

.newly-arrived-box-hover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  opacity: 0;
  transition: all 0.3s ease;
  padding: 15px;
  color: #ffffff;
}

@media only screen and (max-width: 480px) {
  .newly-arrived-box-hover {
    position: static;
    height: auto;
    padding: 0;
    opacity: 1;
    color: #1d363b;
    text-align: center;
  }
  .newly-arrived-box-hover .bg-absolute,
  .newly-arrived-box-hover hr,
  .newly-arrived-box-hover .compare-box a {
    display: none;
  }
  .newly-arrived-box-hover .compare-box,
  .newly-arrived-box-hover .compare-box .price {
    width: 100%;
    float: none;
    text-align: inherit;
  }
  .newly-arrived-box-hover p {
    margin: 0.3em 0;
    line-height: 1.3;
  }
}

.newly-arrived-box-hover a {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 700;
  line-height: normal;
  font-size: 1rem;
}

.newly-arrived-box-hover a:hover {
  text-decoration: none;
}

.newly-arrived-box-hover hr {
  border-top: 1px solid #707070;
  margin: 8px 0px;
}

.newly-arrived-box-hover p {
  font-family: "Open Sans", sans-serif;
  font-size: 0.9rem;
  margin-bottom: 0;
}

.compare-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.compare-box a img {
  margin-right: 11px;
}

.compare-box a {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 10px;
}

.compare-box a:hover {
  text-decoration: none;
